@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.container1 {
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
}

.container1 .slide {
  position: relative;
  width: 100%;
  height: 65vh;
  margin: 0;
  padding: 0;
  z-index: 1;
  box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.925);
}

.texts {
  padding: 0px 15%;
  height: 35vh;
  display: flex;
  color: white;
  background-color: #0a0a0a;
  align-items: center;
  justify-content: space-between;
}

.text {
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: flex-start;
}

.text h2 {
  font-family: "Montserrat Alternates", sans-serif;
  font-size:26px;
}
.text p {
    width: 300px;
    font-family: "Lato";
    font-size: 18px;
    font-weight: 300;
}
